<template>
    <div v-if="options" @click="onClick(options, $event)" :title="options.hint"
        :class="[ 'x-footer-button d-flex align-center', options.class, { 'cursor-pointer': (options.to || options.click) } ]">

        <v-icon v-if="options.icon" v-text="options.icon" size="18" class="mr-1"></v-icon>

        <div v-if="options.label" v-text="options.label"></div>

        <v-img v-if="options.image" :src="options.image" max-width="65"></v-img>
    </div>
</template>

<script>
export default {
    name: 'FooterItem',
    props: {
        options: {
            type: Object
        },
    },
    methods: {
        onClick(options, $event) {
            this.$emit('click', $event);

            if (options && options.click) {
                options.click(options, $event);
            }

            if (options && options.to) {
                if (typeof options.to === 'object') {
                    return this.$router.push(options.to);
                }
                if (options.to.indexOf('/') > -1) {
                    return this.$router.push({ path: options.to });
                }
                this.$router.push({ name: options.to });
            }
        },
    },
}
</script>

<style lang="sass">
.x-footerbar
    .x-footer-button
        border-right: 1px solid rgba(0, 0, 0, 0.2)
        font-size: 0.82rem
        padding: 4px 14px
        user-select: none
        height: 100%
    &.four
        .x-footer-button
            border-right: 1px solid rgba(0, 0, 0, 0.1)

.theme--dark
    .x-footerbar
        .x-footer-button
            border-right: 1px solid rgba(255, 255, 255, 0.2) !important
</style>
