<template>
    <div v-if="options" @click="onClick(options, $event)" :title="options.hint" :class="[ 'x-footer-chat-item d-flex align-center', options.class, { 'cursor-pointer': (options.to || options.click) } ]">
        <v-icon v-if="options.icon" v-text="options.icon" size="24"></v-icon>

        <div v-if="options.label" v-text="options.label" class="hidden-sm-and-down ml-2"></div>
    </div>
</template>

<script>
export default {
    name: 'FooterChatItem',
    props: {
        options: {
            type: Object
        },
    },
    methods: {
        onClick(options, $event) {
            this.$emit('click', $event);

            if (options && options.click) {
                options.click(options, $event);
            }

            if (options && options.to) {
                if (typeof options.to === 'object') {
                    return this.$router.push(options.to);
                }
                if (options.to.indexOf('/') > -1) {
                    return this.$router.push({ path: options.to });
                }
                this.$router.push({ name: options.to });
            }
        },
    },
}
</script>

<style lang="sass">
.x-footer-chat-item
    background-color: rgba(0, 0, 0, 0.1)
    font-size: 0.82rem
    padding: 4px 20px
    user-select: none
    height: 100%

.x-footer-chat-item:hover
    background-color: rgba(0, 0, 0, 0.2)

.theme--dark
    .x-footer-chat-item
        border-left: 1px solid rgba(0, 0, 0, 0.6)
        background-color: rgba(255, 255, 255, 0.2)
    .x-footer-chat-item:hover
        background-color: rgba(255, 255, 255, 0.3)
</style>
